<template>
  <div>
    <fTitle>
      <template #title>Slider 滑块</template>
      <template #introduce>通过拖动滑块在一个固定区间内进行选择</template>
    </fTitle>
    <sTitle>
      <template #title>基础用法</template>
      <template #introduce>在拖动滑块时，显示当前值</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
    <el-row type="flex" justify="space-around">
      <el-col :span="5">
        <div class="grid-content"><span>默认</span></div>
    </el-col>
    <el-col :span="13">
      <div class="grid-content"><el-slider v-model="value1"></el-slider></div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
      <el-col :span="5">
        <div class="grid-content"><span>自定义初始值</span></div>
    </el-col>
    <el-col :span="13">
      <div class="grid-content"><el-slider v-model="value2"></el-slider></div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
      <el-col :span="5">
        <div class="grid-content"><span>隐藏 Tooltip</span></div>
    </el-col>
    <el-col :span="13">
      <div class="grid-content"><el-slider v-model="value3" :show-tooltip="false"></el-slider></div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
      <el-col :span="5">
        <div class="grid-content"><span>格式化 Tooltip</span></div>
    </el-col>
    <el-col :span="13">
      <div class="grid-content"><el-slider v-model="value4" :format-tooltip="formatTooltip"></el-slider></div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
      <el-col :span="5">
        <div class="grid-content"><span>禁用</span></div>
    </el-col>
    <el-col :span="13">
      <div class="grid-content"><el-slider v-model="value5" disabled></el-slider></div>
    </el-col></el-row>
      </div></div>
      <sTitle>
      <template #title>离散值</template>
      <template #introduce>选项可以是离散的</template>
    </sTitle>
      <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
      <el-col :span="5">
        <div class="grid-content"><span>不显示间断点</span></div>
    </el-col>
    <el-col :span="13">
      <div class="grid-content"> <el-slider
      v-model="value1"
      :step="10">
    </el-slider></div>
    </el-col></el-row>    <el-row type="flex" justify="space-around">
      <el-col :span="5">
        <div class="grid-content"><span>显示间断点</span></div>
    </el-col>
    <el-col :span="13">
      <div class="grid-content"><el-slider
      v-model="value2"
      :step="10"
      show-stops>
    </el-slider></div>
    </el-col></el-row>
      </div></div>
      <sTitle>
      <template #title>带有输入框</template>
      <template #introduce>通过输入框设置精确数值</template>
    </sTitle>
      <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
      <el-col :span="18">
        <div class="grid-content">
          <el-slider
      v-model="value6"
      show-input>
    </el-slider>
        </div>
    </el-col></el-row>
      </div></div>
      <sTitle>
      <template #title>范围选择</template>
      <template #introduce>支持选择某一数值范围</template>
    </sTitle>
      <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
      <el-col :span="18">
        <div class="grid-content">
          <el-slider
      v-model="value"
      range
      show-stops
      :max="10">
    </el-slider>
        </div>
    </el-col></el-row>
      </div></div>
      <sTitle>
      <template #title>竖向模式+展示标记</template>
      <template #introduce></template>
    </sTitle>
      <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
      <el-col :span="18">
        <div class="grid-content">
          <el-slider
      v-model="value8"
      vertical
      :marks="marks"
      height="200px">
    </el-slider>
        </div>
    </el-col></el-row>
      </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      value1: 0,
      value2: 50,
      value3: 36,
      value4: 48,
      value5: 42,
      value6: 13,
      value7: [3, 6],
      value8: 0,
      marks: {
        0: '0°C',
        8: '8°C',
        37: '37°C',
        50: {
          style: {
            color: '#1989FA'
          },
          label: this.$createElement('strong', '50%')
        }
      }
    }
  },
  methods: {
    formatTooltip (val) {
      return val / 100
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .el-row{
        border-bottom: 1px dashed #909399;
      }
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;
        text-align: start;
        line-height: 38px;
        }
    }}
</style>
